*{
  box-sizing: border-box;
}

.app{
  width: 400px;
  margin: 0 auto;
  background-color: #fff;
  border: 1px solid silver;
  min-height: 100vh;
}

.brand{
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  padding: 20px 0;
}

.nav{
  border-top: 1px solid silver;
  border-bottom: 1px solid silver;
  display: flex;
  
}

.nav-list{
  width: 400px;
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 10px;
}

.nav-item{
  padding-inline: 10px;
  text-decoration: none;
  color: #000;
  border: 1px solid silver;
}

/* Buat Home, Profile, Contact*/
.section{
  padding: 30px;
}

.section-title{
  margin-top: 0;
}

.section-description{
  line-height: 29px;
}

/* Buat Blog*/
.articles{
  border-top: 1px solid black;
}

.article{
  border-bottom: 1px solid black;
  padding: 20px 0;
}

.article-title{
  margin-top: 0;
}

.article-title a{
  color: black;
  text-decoration: none;
}

.article-title a:hover{
  color: purple;
}

.article-time{
  color: silver;
  text-weight: bold;
}

/* Blog Detail */
.article-img{
  max-width: 100%;
  margin: 10px 0;
}

.article-summary{
  line-height: 27px;
}

.article-source{
  color: blue;
  weight: bold;
}

@media screen and (max-width: 350px) {
  .app{
    background-color:yellow;
  }
}